<!--  -->
<template>
    <!-- scrollToEndscroll.search -->
    <scroll  class="suggest" :data="result" 
        :pullup="pullup"
        @scrollToEnd="searchMore"
        ref="suggest"
        :beforeScroll="beforeScroll"
        @beforeScroll="listScroll"
    >
        <ul class="suggest-list" ref="suggestList">
            <!-- searchHistory  -->
            <li @click="selectItem(item)" class="suggest-item" v-for="item in result"> 
                <div class="icon">
                    <i :class="getIconCls(item)"></i>
                </div>
                <div class="name">
                    <p class="text" v-html="getDisplayName(item)"></p>
                </div>
            </li>
            <loading v-show="hasMore" title=""></loading>
        </ul>
        <div class="no-result-wrapper" v-show="!hasMore && !result.length">
            <no-result title=""></no-result>
        </div>
    </scroll>

</template>

 <script type="text/ecmascript-6">
 import {search} from 'api/search'
 import {ERR_OK} from 'api/config'
 const TYPE_SINGER = 'singer'
//
const PER_PAGE = 20
//2
import {createSong} from 'common/js/song'
// 
import Scroll from 'base/scroll/scroll'
// 
import Loading from 'base/loading/loading'
import Singer from 'common/js/singer'
import {mapMutations, mapActions, mapGetters} from 'vuex'
import NoResult from 'base/no-result/no-result'
import Song from 'common/js/song'
export default {
    
    props: {
        query: {
            type: String,
            default: ''
        },
        showSinger: {
            type: Boolean,
            default: true
        }
    },
    data() {
        return {
            page: 1,
            // 
            result: [],
            // 
            pullup: true,
            // 
            hasMore: true,
            // scroll
            beforeScroll: true
        }
    },
    computed: {
        // gettercomputed
        ...mapGetters(['playHistory'])
    },
    methods: {
        search() {
            // 
            this.page = 1
            this.hasMore = true
            // .
            this.$refs.suggest.scrollTo(0, 0)
            // .
            search(this.query, this.page, this.showSinger, PER_PAGE).then((res) => {
                if(res.code === ERR_OK) {
                    this.result = this._genResult(res.data)
                     // 
                    this._checkMore(res.data)
                }
            })
        },
        // 
        _genResult(data) {
            let ret = []
            // 
            if(data.zhida && data.zhida.singerid) {
                ret.push({...data.zhida, ...{type: TYPE_SINGER}})
            }
            if(data.song) {
                ret = ret.concat(this._narmalizeSongs(data.song.list))
            }
            return ret
        },
        // 
        _narmalizeSongs(list) {
            let ret =[]
            list.forEach((musicData) => {
                if(musicData.songid && musicData.albumid) {
                    ret.push(createSong(musicData))
                   
                }
            })
            // console.log(list)
            return ret
        },
         // 
        _checkMore(data) {
            const song = data.song
            // 2020
            if(!song.list.length || (song.curnum+song.curpage*PER_PAGE) > song.totalnum) {
                this.hasMore = false
            }
        },
        // .
        getIconCls(item) {
            if(item.type === TYPE_SINGER) {
                return 'icon-mine'
            } else {
                return 'icon-music'
            }
        },
        // :
        getDisplayName(item) {
            if(item.type === TYPE_SINGER) {
                return item.singername
            } else {
                // 
                return `${item.name}-${item.singer}`
            }
        },
        // .
        searchMore() {
            // .
            if(!this.hasMore) {
                return
            }
            // 
            this.page++
            search(this.query, this.page, this.showSinger, PER_PAGE).then((res) => {
                if(res.code === ERR_OK) {
                    this.result = this.result.concat(this._genResult(res.data))
                     // 
                    this._checkMore(res.data)
                }
            })
        },
        // 
        selectItem(item) {
            if(item.type === TYPE_SINGER) {
            //  
            const singer = new Singer({
                id: item.singermid,
                name: item.singername
            })
            this.$router.push({
                path: `/search/${singer.id}`
            })
            // console.log(singer)
            this.setSinger(singer)
            } else {
                // console.log(item)
                // elsetodo
                // 
               
                this.savePlayHistory(item)
                // console.log(this.playHistory)
                let list = this.playHistory.map((song) => {
                    return  new Song(song)
                })
                // console.log(list)
                this.selectPlay({
                    // ,localhostgettercomputed
                    list: list,
                    index: 0
                })
            }
            this.$emit('select')
        },
        // 
        ...mapMutations({
            setSinger: 'SET_SINGER'
        }),
        //
        //  
        ...mapActions([
            'selectPlay',
            'savePlayHistory'
        ]),
        // 
        listScroll() {
            this.$emit('listScroll')
        },
        refresh() {
            this.$refs.suggest.refresh()
        }
       
    },
    // 
    watch: {
        query() {
            this.search()
        }
    },
    components: {
        Scroll,
        Loading,
        NoResult
    }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .suggest
    height: 100%
    overflow: hidden
    .suggest-list
      padding: 0 30px
      .suggest-item
        display: flex
        align-items: center
        padding-bottom: 20px
      .icon
        flex: 0 0 30px
        width: 30px
        [class^="icon-"]
          font-size: 14px
          color: $color-text-d
      .name
        flex: 1
        font-size: $font-size-medium
        color: $color-text-d
        overflow: hidden
        .text
          no-wrap()
    .no-result-wrapper
      position: absolute
      width: 100%
      top: 50%
      transform: translateY(-50%)
</style>


