// localstorage
import storage from 'good-storage'
// 15
const SEARCH_KEY = '__search__'
const SEARCH_MAX_LENGTH = 15

const PLAY_KEY = '__play__'
const PLAY_MAX_LENGTH = 80

const FAVORITE_KEY ='__favorite__'
const FAVORITE_MAX_LENGTH = 80
// .actions
//  arr val compare(findindex) maxLen
function insertArray(arr, val, compare, maxLen) {
    const index = arr.findIndex(compare)
    // 
    if(index === 0) {
        return
    }
    // 
    if(index > 0) {
        arr.splice(index, 1)
    }
    // 
    arr.unshift(val)
    // 
    if(maxLen && arr.length > maxLen) {
        arr.pop()
    }
}
// 
export function saveSearch(query) {
    // keySEARCH_KEYvalue
    let searches = storage.get(SEARCH_KEY, [])
    insertArray(searches, query, (item) => {
        return item === query
    }, SEARCH_MAX_LENGTH)
    storage.set(SEARCH_KEY, searches)
    return searches
}
//[]state
 export function loadSearch() {
    //  keySEARCH_KEY[]
     return storage.get(SEARCH_KEY, [])
 }
// .
function deleteArray(arr, compare) {
    const index = arr.findIndex(compare)
    if(index > -1) {
        arr.splice(index, 1)
    }
}
// 
export function deleteSearch(query) {
    // keySEARCH_KEYvalue
    let searches = storage.get(SEARCH_KEY, [])
    deleteArray(searches, (item) => {
        return item === query
    })
    // 
    storage.set(SEARCH_KEY, searches)
    return searches
}
// 
export function clearSearch() {
    storage.remove(SEARCH_KEY)
    return []
}

// 
export function savePlay(song) {
    // keySEARCH_KEYvalue
    let songs = storage.get(PLAY_KEY, [])
    insertArray(songs, song, (item) => {
        return item.mid === song.mid
    }, PLAY_MAX_LENGTH)
    storage.set(PLAY_KEY, songs)
    return songs
}
// 
export function loadPlay() {
    //  JSONP
    return storage.get(PLAY_KEY, [])
}

// 
export function saveFavorite(song) {
    let favoriteSongs = storage.get(FAVORITE_KEY, [])
    insertArray(favoriteSongs, song, (item) => {
        return item.mid === song.mid
    }, FAVORITE_MAX_LENGTH)
    storage.set(FAVORITE_KEY, favoriteSongs)
    return favoriteSongs
}
export function deleteFavorite(song) {
    let favoriteSongs = storage.get(FAVORITE_KEY, [])
    deleteArray(favoriteSongs, (item) => {
        return item.mid === song.mid
    })
    // 
    storage.set(FAVORITE_KEY, favoriteSongs)
    return favoriteSongs
}

export function loadFavorite(song) {
    return storage.get(FAVORITE_KEY, [])
}


